/* App.css */
body {
  font-family: "Poppins", Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #ffffff; /* Set your desired background color here */
}

.App {
  text-align: center;
  padding: 20px;
}

.search-container {
  margin-bottom: 20px;
}

input {
  font-family: "Poppins", Arial, sans-serif;
  padding: 8px;
  margin-right: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 200px;
}

button {
  font-family: "Poppins", Arial, sans-serif;
  padding: 8px 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #ddd;
}

th, td {
  font-family: "Poppins", Arial, sans-serif;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #ddd;
}
